.repo {
	display: block;
	padding: 10px 0;
	color: #000;
	text-decoration: none;
	&__name {
		font-weight: 500;
		font-size: 20px;
		margin-bottom: 8px; }
	&__forked {
		font-size: 12px;
		color: #bebebe;
		margin-bottom: 10px; }
	&__description {
		margin-bottom: 8px; }
	&__footer {
		display: flex;
		font-size: 14px; }
	&__stars, &__lang {
		margin-right: 10px; } }
